import React from 'react'
import {observer} from 'mobx-react-lite'
import {Accordion, Col, Row, Tab, Tabs,} from 'react-bootstrap'
import {dataInsideCard} from "../../../../../generic/Data/DataCard";
import {nanoid} from "nanoid";


export default observer(() => {

    return (
        <Row>
            <Col lg={12}>
                <Accordion>
                    <Accordion.Item eventKey="0">
                        <Accordion.Header
                            style={{color: '#0d6efd'}}></Accordion.Header>
                        <Accordion.Body>
                            <Tabs
                                defaultActiveKey=""
                                transition={false}
                                id="noanim-tab-example"
                                className="mb-3"
                            >
                                {
                                    dataInsideCard.map((s) => (
                                        <Tab key={nanoid()} eventKey={s.header} title={s.header}>
                                            <div key={nanoid()} style={{padding: ' 0 2rem'}}>
                                                <Row>
                                                    <DetailsInside data={s}/>
                                                </Row>
                                            </div>
                                        </Tab>
                                    ))
                                }
                            </Tabs>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </Col>
        </Row>
    )
})
const DetailsInside = observer(({data}: any) => (
    data.inside.map((inside) => (
        <div key={nanoid()}>
            <Col key={nanoid()} lg={6}>
                <div key={nanoid()} style={{
                    padding: '10px',
                    color: 'rgba(0,0,0,0.5)'
                }}>
                    {inside.img}
                </div>
            </Col>
            <Col key={nanoid()} lg={6}>
                <div key={nanoid()} style={{
                    padding: '10px',
                    color: 'rgba(0,0,0,0.5)'
                }}>
                    {inside.body}
                </div>

            </Col>
        </div>
    ))
))
