import React from 'react'
import {observer} from 'mobx-react-lite'
import {Button, Col, Row,} from 'react-bootstrap'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import Rate from "../Rate/Rate";
import CardDescr from "../CardDescr/CardDescr";
import {faStar} from "@fortawesome/free-solid-svg-icons/faStar";
import {nanoid} from "nanoid";

interface ICardContent {
    data: any
}

const faPropIconFaStar = faStar;
export default observer(({data}: ICardContent) => {

    return (
        <Row>
            <Col md={12} lg={2} xl={1}>
                <img
                    alt=""
                    src={data.img}
                    width="60"
                    height="60"
                    style={{borderRadius: '15px'}}
                    className="d-inline-block align-top"
                />
            </Col>
            <Col lg={2}>
                <Row>
                    <Col style={{fontSize: '1rem'}} lg={12}>
                        {data.name}
                    </Col>
                    <Col lg={12}>
                        <FontAwesomeIcon key={nanoid()} icon={faPropIconFaStar}
                                         style={{
                                             color: '#ffca2c',
                                             marginRight: '5px'
                                         }}/>
                        <Rate key={nanoid()} data={data}/>
                    </Col>
                </Row>
            </Col>
            <CardDescr key={nanoid()} data={data}/>
            <div key={nanoid()}>
                <Button key={nanoid()} href={data.link} className={'mob-btn'}>
                     
                </Button>
            </div>
        </Row>
    )
})
