import React, {useState} from 'react'
import { Card,} from 'react-bootstrap'
import './LoanPortfolio.scss'
import {observer} from 'mobx-react-lite'
import loanStore from '../../../../lib/store/pages/Loan-store'
import ResultCard from './ResultCard/ResultCard'

import FloatingLabelCustom from "../../../../generic/Search/FloatingLabelCustomNum/FloatingLabelCustomNum";
import Toaster from "../../../../../core/lib/toaster/toaster";
import {toast} from "react-toastify";
import {AnimationEffect} from "../../../../generic/AnimationEffect/AnimationEffect";


export default observer(() => {

    const [mes, setMec] = useState(null)


    const changeVal = (value) => {
        setMec(value.target.value)
    }

    const clickNum = () => {
        if (Number(mes) < 100) {
            new Toaster({msg: '     ', type: toast.TYPE.WARNING})
        } else {
            loanStore.setMinNumRange(Number(mes))
        }
    }

    const clickNumSec = () => {
        loanStore.setMinNumRange(Number(''))
        setMec('')
    }


    return (
        <AnimationEffect>
            <Card.Body>
                {/*<br/>*/}
                {/*<Card.Title>   </Card.Title>*/}
                {/*<Alert key={'primary'} variant={'light'} className={'loan-portfolio-text'}>*/}
                {/*          */}
                {/*</Alert>*/}
                {/*<br/>*/}
                {/*<br/>*/}
                <FloatingLabelCustom type={'number'} onclickBtn={clickNum} onclickBtnSecond={clickNumSec}
                                     label={' '} value={mes}
                                     onChange={(value) => changeVal(value)}/>
                <ResultCard/>
            </Card.Body>
        </AnimationEffect>
    )
})
