import React from 'react'
import {observer} from 'mobx-react-lite'
import {Col,} from 'react-bootstrap'
import {nanoid} from 'nanoid'

interface ICardDescr {
    data: any
}

export default observer(({data}: ICardDescr) => {

    return (
        data.descr.map((descr) => (
            <Col key={nanoid()} md={4} lg={2}>
                <div  style={{marginBottom: '1rem'}} key={nanoid()}
                     dangerouslySetInnerHTML={{__html: descr.body}}/>
            </Col>
        ))
    )
})
