import React, {useEffect} from 'react'
import {observer} from 'mobx-react-lite'
import {Col, Row} from 'react-bootstrap'
import {nanoid} from 'nanoid'
import loanStore from '../../../../../lib/store/pages/Loan-store'
import {data} from '../../../../../generic/Data/DataCard'

import './ResultCard.scss'
import ModalLoader from '../../../../../generic/ModalLoader/ModalLoader'
import ServiceCard from "../../../../../generic/ServiceCard/ServiceCard";
import {AnimationEffect} from "../../../../../generic/AnimationEffect/AnimationEffect";

export default observer(() => {

    useEffect(() => {
        loanStore.setToastBtn('')
        loanStore.setStateLoader(false)
    }, [])

    return (
        <>
            <ModalLoader/>
            <Row>
                {data.map((data) => (
                    data.min - 1 > loanStore.minNumRange ?
                        null
                        :
                        <Col key={nanoid()} md={3}>
                            <AnimationEffect>
                                <ServiceCard data={data} key={nanoid()}/>
                            </AnimationEffect>
                        </Col>
                ))}
            </Row>
        </>
    )
})
